* {
    margin: 0;
}

.top-bar {
    height: 48px;
    width: 100;
    background-color: #fef7f6;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
}

.contact span:first-child {
    padding-right: 15px;


}

.info span {
    text-align: right;
    padding-right: 15px;
}

.container {
    height: 90.39px;
    width: 100;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.shopping span {
    text-align: right;
    padding-right: 15px;
    font-size: 13px;
    font-weight: 600;
    list-style: none;
    cursor: pointer;
}

.icon-container {
    font-size: 18px;
    display: flex;
    flex-direction: row;

}

.card-bag-icon {
    position: relative;
    padding: 10px;

}

.circle {
    position: absolute;
    background-color: #ff5353;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    top: 0;
    right: 0;
    color: #fff;
    font-size: 8px;
    font-weight: 400;
    line-height: 16px;
    text-align: center;
}

.square {
    background-color: red;
    color: white;
    font-size: 10px;
    font-weight: bold;
    position: relative;
    top: -15px;
    border: 5px solid red;
    border-radius: 15px;
}

.banner {
    height: 530 px;
    width: 100%;
    background: url('https://demo.themeies.com/silon/images/bg-1.jpg');
    display: flex;
    // flex-direction: column;
    background-size: cover;

    h2 {
        display: block;
        margin-bottom: 15px;
        font-weight: 500;
        line-height: 0.8;
        letter-spacing: 2.8px;
        text-transform: uppercase;
    }

    h1 {
        font-size: 40px;
        line-height: 56px;
        margin-bottom: 35px;
    }

    botton {
        background-color: #f15151;
        border-radius: 3px;
        color: #fff;
        text-transform: uppercase;
        display: inline-block;
        font-size: 14px;
        font-weight: 500;
        line-height: 0.8;
        min-width: 170px;
        padding: 20px 25px;
    }
}

>div {
    width: 50%;
}


img {
    height: auto;
    width: 100%;

}


.h2 {
    font-size: 40px;
    line-height: 56px;
    margin-bottom: 35px;
}

.products1 {
    position: relative;
}

.products1 img {
    display: block;

}

.products2 {
    position: relative;
}

.products2 img {
    display: block;

}

.products3 {
    position: relative;
}

.products3 img {
    display: block;

}

.action-links {
    bottom: 60px;
    width: 100%;
    position: absolute;
    left: 30%;
    // visibility: hidden;
    opacity: 0;
    z-index: 10;
    transition: all 300ms ease-in-out;
}

.action-links:hover {
    visibility: visible;
    opacity: 1;


    .ti-eye {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        background: white;
        font-size: 14px;
        display: inline-block;
        text-align: center;
    }

    .ti-heart {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        background: white;
        font-size: 14px;
        display: inline-block;
        text-align: center;

    }

    .ti-heart:before {
        content: "\e634";
    }

    *,
    ::after,
    ::before {
        box-sizing: border-box;
    }

    .ti-search {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        background: white;
        font-size: 14px;
        display: inline-block;
        text-align: center;

    }
}

.list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-content: flex-end;
    text-align: center;

}

.list h2 {
    padding: 20px;
}

.list button {
    background-color: red;
    color: #fff;
    cursor: pointer;
    text-transform: uppercase;
    display: inline-block;
}


